@import "normalizes.scss";
@import "resets.scss";

* {
    box-sizing: border-box; // чтобы вссе padding и margin
 }    // входили в состав размера нашего элемента

@font-face {
    font-family: "GeoramaT";
    src: url('../fonts/Georama-Regular.ttf');
    font-display: swap; //чтобы сначала загрузился станадртный шрифт
    font-weight: regular;
    font-style: normal; }

@font-face {
    font-family: "GeoramaT";
    src: url('../fonts/Georama-Bold.ttf');
    font-display: swap;
    font-weight: bold;
    font-style: normal; }

html { // без точки
    font-family: "GeoramaT"; //надо задвать обоим шрифтам одинаковое font-family, чтобы можно было потом просто настраивать style
    font-weight: regular; }

$color-blue: #4686E2; // переменная
$color-orange: #FB875B;
$color-grey: #D2D2D2;

.container {
    position: relative;
    max-width: 1140px;
    margin: 0 auto; }

.main-section { //фон всей страницы
    position: relative; // чтобы потом назначить overflow
    overflow: hidden; // скрывает все, что заходит за границы секции
    height: 800px;
    background: #F1F1F1;
    padding-top: 20px; // отступ от края к меню
    margin-bottom: 80px;
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between; // размещение меню справа
        height: 67px;
        align-items: center;
        font-weight: bold; }
    &__content-container {
        position: relative;
        display: flex;
        flex-direction: row; }
    &__first-elipse { //позиционирование кругов (блок elipse)
        position: absolute;
        top: 0px;
        right: 280px; }
    // позиционирование второго (большого) круга
    &__second-elipse {
        position: absolute;
        top: 600px;
        right: -150px; } } //минусовые значения чтобы уйти за край фрейма


.logo {
    width: 130px; }

.nav-menu { //пукнты меню размещаем горизонтально
    &__list {
        display: flex; //flexbox
        flex-direction: row; }
    &__item {
        margin-right: 35px; //расстояние между элементами меню
        font-size: 16px;
        &:last-child {
            margin-right: 0px; } }
    a { // декорирование ссылок в nav меню сверху
        text-decoration: none;
        color: black; } }

.left-wrapper {
    position: relative; // если ставим позицию absolute
    // для child элемента, то родительскому нужно поставиь
    // позициюи relative
    &__image {
        position: relative; // надо поставить чтобы работал
        // z-index
        z-index: 3; } // расположение в плоскости по отношению к другим элементам
    &__rectangle-blue {
        position: absolute;
        top: 120px;
        left: -80px;
        width: 526px;
        height: 220px;
        background: $color-blue;
        border-radius: 100px;
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.05);
        // 0px - отклонение по горизонтали, 5px - отклонение по вертикали
        // 10px - радиус размытия, () - цвет
        transform: rotate(-74.84deg);
        z-index: 0; }
    &__rectangle-orange {
        position: absolute;
        top: 130px;
        left: -45px;
        width: 608px;
        height: 205px;
        background: $color-orange;
        border-radius: 100px;
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.05);
        transform: rotate(-51.67deg);
        z-index: 2; }
    &__rectangle-grey {
        position: absolute;
        top: 190px;
        left: 25px;
        width: 560px;
        height: 220px;
        background: $color-grey;
        border-radius: 100px;
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.05);
        transform: rotate(-29.39deg);
        z-index: 1; } }

.icons { // обращаемся к блоку
    display: inline-flex;
    flex-direction: row; // или вместо двух строк display: inline-flex
    &__items {
        display: flex;
        justify-content: center; // выравнивание иконок
        align-items: center;
        height: 34px;
        width: 34px;
        border: 1px solid black;
        border-radius: 100px;
        margin-right: 15px;
        &:last-child {
            margin-right: 0px; }
        i {
            font-size: 20px; } } }
// так как у нас иконочный шрифт, применяем
// характеристики как для текста

.right-wrappter {
    width: 400px;
    position: absolute; //чтобы положение правого блока
    // не зависило от левого
    // в родительском контейнере main-section__content-container ОБЯЗАТЕЛЬНО должно стоять
    // position: relative
    top: 60px; // растояние от края фрейма
    right: 0px;
    z-index: 2;
    &__icon-box {
        margin-bottom: 1px;
        display: flex;
        justify-content: flex-end; } // иконки по правому краю контейнера
    &__title {
        margin-bottom: 50px; } // отодвигаем текст снизу
    &__text {
        font-size: 20px;
        margin-bottom: 34px; } }

.title {
    font-size: 64px;
    position: relative;
    font-weight: bold;
    &__hr { // горизонтальная полоска под имененм
        bottom: 300px;
        width: 90px; // длина полосы
        position: absolute;
        top: 64px; // растояние между текстом и полоской
        margin: 0px; // зачно могут большие Margins
        //border: none
        border: none; // убираем все рамку чтобы сработал border-top
        // и не было рамки с 4х сторон
        border-top: 1px solid black; } }


.buttons {
    display: inline-flex; // выравнили кнопку, иконку и текст чата
    &__icon-btn {
        display: inline-flex; // выравниваем иконку и текст чата
        align-items: center;
        img {
            margin-right: 5px; } }
    &__main-btn {
        margin-right: 15px;
        width: 220px;
        height: 50px;
        left: 923px;
        top: 384px;
        background: $color-orange;
        border-radius: 100px;
        border: none; // убераем рамку (контур вокруг кнопки)
        font-size: 18px; // размер текста
        color: white; // цвет тееста
        font-weight: bold; }
    &__text {
        font-size: 18px; } }

// 1й внелний элипс
.elipse {
    width: 125px;
    height: 125px;
    background: linear-gradient(184.51deg,
    rgba(70, 134, 226, 0.497) -2.23%,
    rgba(181, 135, 143, 0.561526) 26.71%,
    rgba(251, 135, 91, 0.602) 44.86%,
    rgba(242, 157, 124, 0.442607) 71.53%,
    rgba(217, 217, 217, 0) 106.68%) {}
    transform: rotate(-4.95deg);
    border-radius: 200px;
    display: flex;
    justify-content: center; //  позиционируем внутренний круг */
    align-items: center;
    //модификатор
    &_big {
        width: 388px;
        height: 388px; }
    &__substrate {
        width: 79px;
        height: 79px;
        background: #F1F1F1;
        border-radius: 200px;
        //модификатор
        &_big {
            width: 255px;
            height: 255px; } } }

//вторая секция
.second-section {
    overflow: hidden;
    position: relative;
    min-height: 800px;
    &__title {
        margin-bottom: 34px; }
    &__courses {
        display: inline-flex;
        justify-content: space-between;
        margin-bottom: 100px;
        margin-right: 20px; }
    &__items {
        margin-right: 20px;
        &:last-child {
            margin-right: 0px; } }
    &__advantages {
        display: flex;
        justify-content: space-between; }
    &__first-elipse {
        position: absolute;
        top: 380px;
        left: -20px;
        z-index: -1; } }

.elipse__substrate {
    &_white {
        background: white; } }

.courses {
    position: relative; // Т.к. в  &__img position:absolute
    width: 367px;
    height: 367px;
    background: $color-grey;
    border-radius: 20px;
    display: flex;
    align-items: flex-end;
    font-weight: bold;
    &__img {
        height: 367px; }
    &__text {
        width: 320px; //высота контейнера с текстом
        font-size: 40px;
        color: white;
        text-align: center;
        transform: rotate(90deg);
        position: absolute;
        top: 165px;
        left: 159px; } }

.courses_orange { // модиикаторы
    background: $color-orange; }

.courses_blue { // модиикаторы
    background: $color-blue; }

.advantages {
    display: inline-flex;
    align-items: center;
    &__img {
        margin-right: 20px;
        width: 78px; }
    &__text {
        font-size: 16px;
        span {
            font-size: 30px;
            font-weight: bold; } } }

// третья секция
.third-section {
    position: relative; // чтобы работал z-index
    min-height: 800px;
    background: #F1F1F1;
    overflow: hidden;
    z-index: 0; // надо указать 0, чтобы элипс не прятался под фоном

    &__container {
        display: flex { // не используем indline-flex для контейнера,
            // т.к. собетрся центральное выравнивание контейнера, которое
 }            // мы задавали  c margin:0 auto
        flex-direction: row { //размещаем левый и правый блок
 }            //горизонтально друг с другом
        align-items: center;
        justify-content: space-between { //задаем одинаковое растояние между
 }            // левым и правым блоком
        padding-left: 167px { // отодбивагем левый заголовок от
 }            //границы левого контейнера
        padding-top: 128px; } // отодвигаем правый блок от верхнего края контейнера
    &__right-wrapper {
        max-width: 480px; } //ширина правого контейнера
    &__right-title {
        font-size: 40px;
        color: $color-blue;
        margin-bottom: 30px;
        font-weight: bold; }

    &__text {
        font-size: 16px;
        margin-bottom: 40px; }
    &__first-elipse {
        position: absolute;
        top: 317px;
        left: -27px;
        z-index: -1; }
    &__figure {
        height: 240px;
        width: 240px;
        position: absolute;
        top: 120px;
        left: 80px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        top: -66px;
        right: -139px; }
    &__figure-big {
        position: absolute; // чтобы фигура не уходила под подложку секции
        top: 580px;
        right: -66px; } }


.elipse_medium {
    height: 300px;
    width: 300px; }
.elipse__substrate_medium {
    height: 197px;
    width: 197px; }

//footer
.footer {
    position: relative;
    &__img {
        position: absolute;
        left: -250px;
        bottom: 0px; } // чтобы рисунок был прижат к нижнему краю
    &__container {
        min-height: 360px;
        // нужно задавать высоту в контейнере, чтобы по вертикале
        ///  выравнить элементы, т.к. нужен размер пространства, в
        /// в котором нужно выравнивать. Если бы задавали размеры footer, то высота контейнера была бы равно размеру внутренних элементов
        // и не было пространства, в котором выравнивать. А теперь footer будет растягиваться под размера контента в container */
        display: flex;
        align-items: center;
        justify-content: flex-end; }
    &__title {
        position: relative;
        margin-bottom: 30px; }
    h5 {
        margin-bottom: 10px; } }

