// Адаптив под горизонтальный телефон
@media (max-width: 767px) {
    .container {
        width: 540px; }
    .nav-menu {
        position: fixed; //чтобы меню был всегда прибито к определенному месту
        top: 0; //координаты места, к которому будет прибито меню
        right: -100%; //элемент скроется за границы экрана
        background: $color-blue;
        height: 100vh;
        z-index: 20;
        width: 50%;
        transition: 0.6s all; // анимация
        &__list {
            flex-direction: column; //выстроить пункты меню вертикально
            justify-content: space-evenly; // задает одинаковое растояние между элементамм
            align-items: center; //так как поменяли направление с помощью flex-direction:column, горизонтальное выравнние надо делать при помщи align-items
            height: 100%; } //надо задать, чтобы сработал space-evenly
        &__item {
            margin-right: 0;
            font-size: 20px; } }

    /* margin-right: 35px */
    .hamburger { // гамбургер
        width: 40px;
        position: absolute; // position:relative родительскому эл-ту не надо ставить, тк. позиционируем от body
        top: 42px;
        right: 30px;
        height: 22px; //задаем высоту, чтобы была область куда нажимать пальцем (чтобы она не ограничивалась только шириной полосок)
        z-index: 30;
        span { // если обращение к тегу, точку ставить не надо
            height: 4px;
            width: 100%;
            background: black;
            display: block; // span - строчный элемент
            margin-bottom: 6px; //лучше устанавливать четное число (удобно потом настраивать крестик)
            transition: 1s all; // время анимации (переход из гамбурега в крестик)
            &:last-child {
                margin-bottom: 0px; } } }
    .hamburger_active { // модификатор для гамбурга (из гамбурега в крестик при наведении)
        span {
            margin-bottom: -3px; //уменьшаем ранее установленные margin-bottom
            &:nth-child(1) { //псевдокласс для обращения к отдельным элементам, номер элемента в ()
                transform: translateY(3px) rotate(-45deg) { // поворот палочки
 } }                    // translateY смещает элемент по оси Y
            &:nth-last-child(2) {
                display: none; } // вторая палочка пропадает
            &:nth-child(3) {
                transform: translateY(3px) rotate(45deg); } } } // повротов палочки
    //чтобы проверить как работает, в консоли разработчика в тег
    // div class="hamburger" нужно вручную дописать модификатор hamburger-active

    .nav-menu_active {
        right: 0px; }



    // чтобы выезжало меню справа
    /// .nav-menu_active  - модификатор (выезжающее меню), нужен только для того чтобы меню появлялось
    //  // стилизуем в классе .nav-menu (выше)
    //чтобы проверить как работает, в консоли разработчика в тег
 }    // <nav class="main-section__nav nav-menu"> нужно вручную дописать модификатор nav-menu_active




